import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { CategoriesService, Product, ProductsService } from '@bluebits/products';
import { MessageService } from 'primeng/api';
import { timer } from 'rxjs';

@Component({
  selector: 'admin-products-form',
  templateUrl: './products-form.component.html',
  styles: [
  ]
})
export class ProductsFormComponent implements OnInit {
  editmode = false;
  form: FormGroup;
  isSubmitted = false;
  categories = []; //
  imageDisplay: string | ArrayBuffer; //user
  currentProductId: string; //curremt Product id, 

  constructor(
    private formBuilder: FormBuilder,
    private productsService: ProductsService,
    private categoriesService: CategoriesService,
    private messageService: MessageService,
    private location: Location,
    private route: ActivatedRoute
    ) { }

  ngOnInit(): void {
    this._initForm();
    //categories from server
    this._getCategories();
    //
    this._checkEditMode();
  }

  //edieMode, id, productForm 
  private _checkEditMode(){
    //url path  id, appModule  route => path: 'products/form/:id',
    this.route.params.subscribe((params) => {
      if(params.id){
        this.editmode = true;
        this.currentProductId = params.id; // product id
        //id product, productForm 
        this.productsService.getProduct(this.currentProductId).subscribe((productFromServer) => {
          //this.productForm JS Getter: get productForm() === this.form.controls;
          //this.productForm.name === this.form.controls.name 
          this.productForm.name.setValue(productFromServer.name);
          //category ,  category object,  id, idtext
          this.productForm.category.setValue(productFromServer.category.id);
          this.productForm.brand.setValue(productFromServer.brand);
          this.productForm.price.setValue(productFromServer.price);
          this.productForm.countInStock.setValue(productFromServer.countInStock);
          this.productForm.isFeatured.setValue(productFromServer.isFeatured);
          this.productForm.description.setValue(productFromServer.description);
          this.productForm.richDescription.setValue(productFromServer.richDescription);
          //imageDisplayuser, imageDisplay, (productFromServer.image)
          this.imageDisplay = productFromServer.image; 
          
          //for product image, product image, edit mode image, user
          // Validators  edit mode image
          //updateValueAndValidity: refresh image element
          this.productForm.image.setValidators([]);
          this.productForm.image.updateValueAndValidity();
        })
      }
    })
  }

  private _initForm(){
    this.form = this.formBuilder.group(
      {
        name: ['', Validators.required],
        brand: ['', Validators.required],
        price: ['', Validators.required],
        category: ['', Validators.required],
        countInStock: ['', Validators.required],
        description: ['', Validators.required],
        richDescription: [''],
        //product image, edit mode image, user
        // edit mode validator 
        image: ['', Validators.required],
        isFeatured: [false], //, 
      }
    )
  }

  //onSubmit(), this.editmode API
  onSubmit(){
    this.isSubmitted = true; //userisSubmittedT, ,  *ngIf="productForm.image.invalid && isSubmitted"
    if(this.form.invalid)
      return;
    
    //FormData(JS)  FormData FormControl  API
    const productFormData = new FormData();

    // productFormData
    //key & value, key:appendproductFormData, values:formcontrol name    
    productFormData.append('name', this.productForm.name.value);
    productFormData.append('brand', this.productForm.brand.value);
    productFormData.append('price', this.productForm.price.value);
    productFormData.append('countInStock', this.productForm.countInStock.value);
    productFormData.append('category', this.productForm.category.value);
    productFormData.append('isFeatured', this.productForm.isFeatured.value);
    productFormData.append('description', this.productForm.description.value);
    productFormData.append('richDescription', this.productForm.richDescription.value);
    productFormData.append('image', this.productForm.image.value);
    
    //
    if (this.editmode) {
      this._updateProduct(productFormData);
    } else {
      // productFormData   Product , 
      this._addProduct(productFormData);
    }
  }

  private _updateProduct(productFormData: FormData) {
    //product product id  this.productsService.updateProduct()
    this.productsService.updateProduct(productFormData, this.currentProductId).subscribe(
      () => {
        this.messageService.add({
          severity: 'success',
          summary: 'Success',
          detail: 'Product is updated!'
        });
        timer(2000)
          .toPromise()
          .then(() => {
            this.location.back();
          });
      },
      () => {
        this.messageService.add({
          severity: 'error',
          summary: 'Error',
          detail: 'Product is not updated!'
        });
      }
    );
  }

  onCancle(){
    this.location.back();
  }

  //categories 
  private _getCategories(){
    this.categoriesService.getCategories().subscribe(categoriesFromServer => {
      this.categories = categoriesFromServer;
    })
  }

  get productForm(){
    return this.form.controls;
  }

  onImageUpload(event){
    const file = event.target.files[0];  //user file()
    if (file) {
      //Submit a new Product as FormData
      //imageformControl, patchValuepatchform
      this.form.patchValue({ image: file });
      //patch
      this.form.get('image').updateValueAndValidity();
      const fileReader = new FileReader(); //file
      //, imageDisplay,   fileReader.readAsDataURL(file); 
      fileReader.onload = () => {
        this.imageDisplay = fileReader.result;
      };
      fileReader.readAsDataURL(file); //user file
    }
  }

  private _addProduct(productData: FormData) {
    this.productsService.createProduct(productData).subscribe(
      (product: Product) => {
        this.messageService.add({
          severity: 'success',
          summary: 'Success',
          detail: `Product ${product.name} is created!`
        });
        timer(2000)
          .toPromise()
          .then(() => {
            this.location.back();
          });
      },
      () => {
        this.messageService.add({
          severity: 'error',
          summary: 'Error',
          detail: 'Product is not created!'
        });
      }
    );
  }

}
