import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable } from 'rxjs';
import { LocalstorageService } from './localstorage.service';
import { environment } from '@env/environment';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {

  constructor(private localstorageToken: LocalstorageService) { }
  
  // postman , postman Authorization: `Bearer ${token}`
  // http requests  intercep()request,  Authorization: `Bearer ${token}`
  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    //localstorage  Token
    const token = this.localstorageToken.getToken();
    //interceptor req, http request URL(environment.apiUrl)
    // http request
    const isAPIUrl = request.url.startsWith(environment.apiUrl);

    //Authorization: `Bearer ${token}`  http request  header,  routes  
    if(token && isAPIUrl){
      request = request.clone({
        setHeaders: {
          Authorization: `Bearer ${token}`
        }
      });
    }
    // req 
    return next.handle(request);
  }
}
