import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { LocalstorageService } from './localstorage.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate{

  constructor(private router: Router, private localStorageToken: LocalstorageService) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    const token = this.localStorageToken.getToken();
    //localStorage  Token
    if(token){
      //JWT token , 1:token header, 2:token(userID, isAdmon, iat:issued at time, exp:), 3:
      //token., token, token.split('.'):., [1](,array 0 based index)
      // JS  atob()  JSON.parse() 
      const tokenDecode = JSON.parse(atob(token.split('.')[1]));
      
      //token isAdmin  true  token 
      if(tokenDecode.isAdmin && !this._tokenExpired(tokenDecode.exp))
        //T, app.module serviceroutesT(routes)
        return true;
    }
    
    //token 
    this.router.navigate(['/login']);
    return false;
  }

  //token
  private _tokenExpired(expiration): boolean {
    //Math.floor(new Date().getTime() / 1000): 
    return Math.floor(new Date().getTime() / 1000) >= expiration;
  }
}
