import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '@env/environment'; //environments folder 
import { User } from '../models/user';
import { map } from 'rxjs/operators';  

//
import * as countriesLib from 'i18n-iso-countries';
declare const require;

@Injectable({
  providedIn: 'root'
})
export class UsersService {
  //environments folder ,  api , environment.apiUrl === http://localhost:3000/api/v1/
  apiURLUsers = environment.apiUrl + 'users';

  constructor(private http: HttpClient) {
    //countries 
    countriesLib.registerLocale(require('i18n-iso-countries/langs/en.json'));
  }

  getUsers(): Observable<User[]> {
    return this.http.get<User[]>(this.apiURLUsers);
  }

  //Observable<User>  server 
  getUser(userId:string): Observable<User>{
    //<User>  server 
    return this.http.get<User>(`${this.apiURLUsers}/${userId}`)
  }

  createUser(user: User): Observable<User> {
    return this.http.post<User>(this.apiURLUsers, user);
  }

  updateUser(user: User): Observable<User> {
    return this.http.put<User>(`${this.apiURLUsers}/${user.id}`, user);
  }

  deleteUser(userId: string): Observable<any> {
    return this.http.delete<any>(`${this.apiURLUsers}/${userId}`);
  }

  //(array key & vlaue)
  getCountries(): { id: string; name: string }[] {
    //countriesLib.getNames('en', { select: 'official' }): (array)
    //Object.entries(): , map, [0], [1]
    //map[{'AU','Australia'},{'USA','United States'}, ...]
    return Object.entries(countriesLib.getNames('en', { select: 'official' }))
    .map((entry) => {
      return {
        id: entry[0],
        name: entry[1]
      };
    });
  }

  //countryKey (value)
  getCountry(countryKey: string): string {
    return countriesLib.getName(countryKey, 'en');
  }

  // order service 
  getUsersCount(): Observable<number> {
    return this.http
      .get<number>(`${this.apiURLUsers}/get/count`)
      .pipe(map((objectValue: any) => objectValue.userCount));
  }


}
