import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Order, OrdersService } from '@bluebits/orders';
import { MessageService } from 'primeng/api';
import { ORDER_STATUS } from '../order.constant';

@Component({
  selector: 'admin-orders-detail',
  templateUrl: './orders-detail.component.html',
  styles: [
  ]
})
export class OrdersDetailComponent implements OnInit {
  order: Order;
  orderStatuses = []; //ORDER_STATUS, order-detailOrder Status
  selectedStatus: any; //,  any
  
  constructor(
    private orderService: OrdersService,
    private messageService: MessageService,
    private route: ActivatedRoute, //params.id 
  ) {}

  ngOnInit(): void {
    this._getOrder(); //order data
    this._mapOrderStatus(); //ORDER_STATUS, order-detailOrder Status
  }

  private _mapOrderStatus() {
    this.orderStatuses = Object.keys(ORDER_STATUS).map((key) => {
      return {
        id: key,
        name: ORDER_STATUS[key].label
      };
    });
  }

  onStatusChange(event) {
    //console.log(event.value) =>  status (optionValue="id") 
    this.orderService.updateOrder({ status: event.value }, this.order.id).subscribe(
      () => {
        this.messageService.add({
          severity: 'success',
          summary: 'Success',
          detail: 'Order is updated!'
        });
      },
      () => {
        this.messageService.add({
          severity: 'error',
          summary: 'Error',
          detail: 'Order is not updated!'
        });
      }
    );
   
  }

  private _getOrder(){
    this.route.params.subscribe((params) => {
      if(params.id){
        this.orderService.getOrder(params.id).subscribe((orderFromDB) => {
          this.order = orderFromDB;
          this.selectedStatus = orderFromDB.status; //status
        })
      }
    })
    
  }

}
