import { Injectable } from '@angular/core';

const TOKEN = 'jwtToken';

@Injectable({
  providedIn: 'root'
})
export class LocalstorageService {
  setToken(data){
    localStorage.setItem(TOKEN, data);
  }

  removeToken(){
    localStorage.removeItem(TOKEN);
  }

  getToken():string{
    return localStorage.getItem(TOKEN);
  }
}
