import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '@env/environment'; //environments folder 
import { User } from '../models/user';
import { LocalstorageService } from './localstorage.service';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  //environments folder ,  api , environment.apiUrl === http://localhost:3000/api/v1/
  apiURLUsers = environment.apiUrl + 'users';
  
  constructor(private http: HttpClient, private token: LocalstorageService, private router: Router) { }

  login(userEnterEmail:string, userEnterPassword:string):Observable<User>{
    return this.http.post<User>(`${this.apiURLUsers}/login`, {email:userEnterEmail, password:userEnterPassword});
  }

  logout(){
    this.token.removeToken();
    this.router.navigate(['/login']);
  }
}
