import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { LocalstorageService } from '../../services/localstorage.service';

@Component({
  selector: 'users-login',
  templateUrl: './login.component.html',
  styles: [
  ]
})
export class LoginComponent implements OnInit {
  loginFormGroup: FormGroup;
  isSubmitted = false;
  authError = false; //
  authMessage = 'Email or Password are wrong';

  constructor(
    private formBuilder:FormBuilder,
    private authService: AuthService,
    private localstorageService: LocalstorageService,
    private router: Router
    ) { }

  ngOnInit(): void {
    this.initLoginForm();
  }

  initLoginForm(){
    this.loginFormGroup = this.formBuilder.group({
      email: ['', Validators.required, Validators.email],
      password: ['', Validators.required]
    })
  }

  onSubmit(){
    this.isSubmitted = true;

    //
    if(this.loginFormGroup.invalid) 
      return;
   
    //this.loginForm get loginForm() this.loginFormGroup.controls
    //this.loginFormGroup.controls , ex.email  user 
    // formControlName="email"  email 
    this.authService.login(this.loginForm.email.value, this.loginForm.password.value)
    .subscribe((userFromDB) => {
      //
      this.authError = false; // Email or Password are wrong 
      this.localstorageService.setToken(userFromDB.token);
      this.router.navigate(['/']); //homepage
      console.log(userFromDB);
    },
    //user ,  token 
    (error:HttpErrorResponse) => {
      //T, 
      this.authError = true;
      if (error.status !== 400) {
        this.authMessage = 'Error in the Server, please try again later!';
      }
    })
  }

  get loginForm(){
    return this.loginFormGroup.controls;
  }

}
